@font-face {
  font-family: "TT Firs Neue";
  src: url("../..//fonts/TTFirsNeue/TTFirsNeue-Regular.woff2"), url("../../fonts/TTFirsNeue/TTFirsNeue-Regular.woff");
  font-style: normal;
  font-weight: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../../fonts/Roboto/Roboto-Regular.woff2"), url("../../fonts/Roboto/Roboto-Regular.woff");
  font-style: normal;
  font-weight: normal;
  font-display: swap;
}
@font-face {
  font-family: "TT Firs Neue";
  src: url("../../fonts/TTFirsNeue/TTFirsNeue-Bold.woff2"), url("../../fonts/TTFirsNeue/TTFirsNeue-Bold.woff");
  font-style: normal;
  font-weight: bold;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../../fonts/Roboto/Roboto-Bold.woff2"), url("../../fonts/Roboto/Roboto-Bold.woff2");
  font-style: normal;
  font-weight: bold;
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

html,
body,
.ui-widget,
.ui-widget input,
.ui-widget select,
.ui-widget textarea,
.ui-widget button {
  /* font-family: "Open Sans", sans-serif; */
  scroll-behavior: smooth;
}

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

a {
  text-decoration: none;
  cursor: pointer;
  outline: none;
}

button {
  cursor: pointer;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
p {
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}

img {
  max-width: 100%;
}

.container {
  width: 1290px;
  margin: 0 auto;
}

.main {
  position: relative;
  margin-bottom: 69px;
}

.header {
  position: relative;
  padding-top: 25px;
  background-color: #202020;
}
.header .container {
  display: flex;
  justify-content: space-between;
}
.header__link-logo {
  outline: none;
}
.header__nav {
  margin-left: 70px;
  padding-top: 5px;
}
.header__list {
  display: flex;
}
.header__item:not(:last-child) {
  margin-right: 79px;
}
.header__link {
  position: relative;
  z-index: 2;
  color: #fff;
  font-family: "TT Firs Neue";
  font-weight: normal;
  font-size: 16px;
  transition-duration: 0.3s;
  transition-timing-function: ease;
}
.header__link:hover {
  color: #FF6E30;
}
.header__link:after {
  content: "";
  position: absolute;
  left: -6px;
  top: -3px;
  z-index: -1;
  width: calc(100% + 12px);
  height: calc(100% + 6px);
  background-color: #BC572B;
  transform: scaleX(0);
  transition-duration: 0.3s;
  transition-timing-function: ease;
}
.header__link:focus:after {
  transform: scaleX(1);
}
.header__link:focus {
  outline: none;
}
.header__link:active {
  color: #CE4307;
  outline: none;
  background-color: inherit;
}
.header__link:active::after {
  display: none;
}
.header__list-socials {
  display: flex;
}
.header__link-socials {
  display: inline-block;
  width: 50px;
  height: 50px;
  background-repeat: no-repeat;
  background-position: 95% 5%;
  transition-duration: 0.3s;
  transition-timing-function: ease;
  outline: none;
}
