.works {
  margin-top: 32px;
  margin-bottom: 66px;
}
.works > .container {
  display: grid;
  grid-template: repeat(3, auto)/630px repeat(2, 300px);
  column-gap: 30px;
  grid-row-gap: 23px;
}
.works__banner {
  position: relative;
  grid-column: 1/2;
  grid-row: 1/4;
  margin-top: 10px;
  padding-top: 405px;
  padding-right: 145px;
  padding-left: 60px;
  background-repeat: no-repeat;
  background-image: url(../../images/desc/works-camera.jpg);
}
.works__banner > * {
  color: #fff;
}
.works__hashtag {
  position: absolute;
  top: 14px;
  right: 17px;
  padding: 7px 14px;
  background-color: #333;
}
.works__banner-subtitle {
  font-family: "Roboto";
  font-weight: normal;
  font-size: 16px;
}
.works__title {
  margin-bottom: 18px;
  font-family: "TT Firs Neue";
  font-weight: bold;
  font-size: 36px;
  line-height: 39px;
}
.works__link {
  position: relative;
  display: inline-block;
  padding: 12px 19px;
  border: 3px solid transparent;
  text-align: center;
  font-family: "Roboto";
  font-weight: bold;
  font-size: 16px;
  background-color: #FF6E30;
  transition-duration: 0.3s;
  transition-timing-function: ease;
  outline: none;
}
.works__link:hover {
  background-color: #BC572B;
}
.works__link:focus {
  border-color: #CE4307;
}
.works__link:active {
  background-color: #CE4307;
}
.works__subtitle {
  grid-column: 2/4;
  font-family: "TT Firs Neue";
  font-weight: bold;
  font-size: 24px;
}
.works__picture-1 {
  grid-column: 2/4;
}
.works__picture-2 {
  grid-column: 2/3;
}
.works__picture-3 {
  grid-column: 3/4;
}

.studio {
  padding-top: 30px;
  padding-bottom: 39px;
  color: #fff;
  background-color: #202020;
}
.studio__grid {
  display: grid;
  grid-template-columns: 630px 630px;
  grid-template-rows: auto auto;
  gap: 30px;
}
.studio__title {
  font-family: "TT Firs Neue";
  font-weight: bold;
  font-size: 24px;
}
.studio__text {
  padding-top: 3px;
  font-family: "Roboto";
  font-weight: normal;
  font-size: 16px;
  line-height: 28px;
}
.studio__rigth {
  padding-top: 3px;
}
.studio__subtitle {
  padding-top: 20px;
  font-family: "TT Firs Neue";
  font-weight: normal;
  font-size: 24px;
  line-height: 22px;
}
.studio__form {
  display: flex;
  justify-content: space-between;
  padding-top: 17px;
}
.studio__input {
  width: 474px;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 23px;
}
.studio__input::placeholder {
  font-family: "Roboto";
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  color: #CACACA;
}
.studio__submit {
  padding-right: 20px;
  padding-left: 20px;
  font-family: "Roboto";
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  color: #fff;
  background-color: #FF6E30;
  border: 3px solid transparent;
  transition-duration: 0.3s;
  transition-timing-function: ease;
  outline: none;
}
.studio__submit:hover {
  background-color: #BC572B;
}
.studio__submit:focus {
  border-color: #CE4307;
}
.studio__submit:active {
  background-color: #CE4307;
}

.projects {
  padding-top: 67px;
}
.projects__grid {
  display: grid;
  grid-template: repeat(2, 300px)/630px repeat(2, 300px);
  gap: 30px;
  padding-top: 12px;
}
.projects__list-top {
  display: grid;
  grid-template-columns: repeat(3, 190px);
  gap: 30px;
}
.projects__item-top {
  position: relative;
}
.projects__title {
  font-family: "TT Firs Neue";
  font-weight: bold;
  font-size: 24px;
}
.projects__hashtag {
  position: absolute;
  top: 7px;
  right: 8px;
  padding: 6px 14px;
  color: #fff;
  font-family: "Roboto";
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  background-color: #333;
}
.projects__text {
  margin-bottom: 8px;
  padding-top: 14px;
  font-family: "Roboto";
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: #202020;
}
.projects__date {
  padding-left: 18px;
  font-family: "Roboto";
  font-weight: normal;
  font-size: 12px;
  color: #999;
  background-image: url("data:image/svg+xml,%3Csvg width='12' height='14' viewBox='0 0 12 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M10.6667 1.75004H10.0833V0.583374H8.91667V1.75004H3.08334V0.583374H1.91667V1.75004H1.33334C0.691672 1.75004 0.166672 2.27504 0.166672 2.91671V12.25C0.166672 12.8917 0.691672 13.4167 1.33334 13.4167H10.6667C11.3083 13.4167 11.8333 12.8917 11.8333 12.25V2.91671C11.8333 2.27504 11.3083 1.75004 10.6667 1.75004ZM10.6667 12.25H1.33334V4.66671H10.6667V12.25Z' fill='%23999999'/%3E%3C/svg%3E%0A");
  background-repeat: no-repeat;
}
.projects__banner-first {
  position: relative;
  grid-column: 2/4;
  padding-top: 23px;
  padding-right: 305px;
  padding-left: 30px;
  background-image: url(../../images/desc/projects4.jpg);
  background-repeat: no-repeat;
}
.projects__banner-first > .projects__date {
  color: #fff;
}
.projects__subtitle {
  margin-bottom: 5px;
  padding-top: 5px;
  font-family: "TT Firs Neue";
  font-weight: bold;
  font-size: 24px;
  line-height: 24px;
  color: #fff;
}
.projects__banner-text {
  margin-bottom: 70px;
  font-family: "Roboto";
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: #fff;
}
.projects__link {
  padding: 13px 20px;
  font-family: "Roboto";
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  color: #fff;
  border: 3px solid #FF6E30;
  transition-duration: 0.3s;
  transition-timing-function: ease;
}
.projects__link:hover {
  border: 3px solid transparent;
  background-color: #FF6E30;
}
.projects__link:focus {
  background-color: #BC572B;
}
.projects__link:active {
  border: 3px solid transparent;
  background-color: #CE4307;
}
.projects__banner-second {
  position: relative;
  grid-column: 1/2;
  padding-top: 23px;
  padding-right: 305px;
  padding-left: 30px;
  background-image: url(../../images/desc/projects5.jpg);
  background-repeat: no-repeat;
}
.projects__banner-second > .projects__banner-text {
  margin-bottom: 115px;
}
.projects__list-bottom {
  display: grid;
  grid-template-columns: 300px 300px;
  gap: 30px;
}

.contacts {
  padding-top: 67px;
}
.contacts__title {
  margin-bottom: 25px;
  font-family: "TT Firs Neue";
  font-weight: bold;
  font-size: 24px;
}
.contacts__grid {
  display: grid;
  grid-template: 460px/850px 410px;
  gap: 30px;
}
.contacts [class*=ground-pane] {
  filter: grayscale(1);
}
.contacts__yandex-map {
  position: relative;
}
.contacts__yandex-info {
  position: absolute;
  z-index: 1;
  height: 100%;
  width: 410px;
  padding-top: 174px;
  padding-right: 50px;
  padding-left: 60px;
  background-color: rgba(32, 32, 32, 0.85);
}
.contacts__yandex-info_close {
  position: absolute;
  top: 4%;
  right: 5%;
  width: 20px;
  height: 20px;
  border: none;
  background-color: transparent;
  background-image: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='10' cy='10' r='10' fill='%23666666'/%3E%3Crect x='6.20712' y='5.5' width='12' height='1' transform='rotate(45 6.20712 5.5)' fill='white'/%3E%3Crect x='5.5914' y='13.9854' width='12' height='1' transform='rotate(-45 5.5914 13.9854)' fill='white'/%3E%3C/svg%3E%0A");
  background-repeat: no-repeat;
}
.contacts__yandex-subtitle {
  margin-bottom: 5px;
  font-family: "TT Firs Neue";
  font-weight: normal;
  font-size: 24px;
  line-height: 22px;
  color: #fff;
}
.contacts__text {
  margin-bottom: 3px;
  font-family: "Roboto";
  font-weight: normal;
  font-size: 16px;
  line-height: 28px;
  color: #cacaca;
}
.contacts__link {
  padding-left: 22px;
  font-family: "TT Firs Neue";
  font-weight: normal;
  font-size: 16px;
  line-height: 21px;
  color: #fff;
  background-image: url(../../images/phone.svg);
  background-repeat: no-repeat;
}
.contacts__content {
  margin-top: -11px;
}
.contacts__content-subtitle {
  font-family: "TT Firs Neue";
  font-weight: normal;
  font-size: 24px;
  line-height: 31px;
}
.contacts__content-text {
  margin-bottom: 15px;
  font-family: "Roboto";
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: #999;
}
.contacts__form {
  display: flex;
  flex-direction: column;
}
.contacts__form-label {
  margin-bottom: 20px;
}
.contacts__input {
  width: 100%;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 23px;
  background-color: #E9E9E9;
  border: none;
  font-family: "Roboto";
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  color: #999;
}
.contacts__comment {
  width: 100%;
  height: 150px;
  margin-bottom: 20px;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 23px;
  background-color: #E9E9E9;
  border: none;
}
.contacts__comment[placeholder] {
  font-family: "Roboto";
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  color: #999;
}
.contacts__submit {
  width: 126px;
  padding-top: 15px;
  padding-bottom: 11px;
  padding-right: 20px;
  padding-left: 20px;
  font-family: "Roboto";
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  color: #fff;
  background-color: #FF6E30;
  border: 3px solid transparent;
  transition-duration: 0.3s;
  transition-timing-function: ease;
  outline: none;
}
.contacts__submit:hover {
  background-color: #BC572B;
}
.contacts__submit:focus {
  border-color: #CE4307;
}
.contacts__submit:active {
  background-color: #CE4307;
}

.header {
  position: relative;
  padding-top: 25px;
  background-color: #202020;
}
.header .container {
  display: flex;
  justify-content: space-between;
}
.header__link-logo {
  outline: none;
}
.header__nav {
  margin-left: 70px;
  padding-top: 5px;
}
.header__list {
  display: flex;
}
.header__item:not(:last-child) {
  margin-right: 79px;
}
.header__link {
  position: relative;
  z-index: 2;
  color: #fff;
  font-family: "TT Firs Neue";
  font-weight: normal;
  font-size: 16px;
  transition-duration: 0.3s;
  transition-timing-function: ease;
}
.header__link:hover {
  color: #FF6E30;
}
.header__link:after {
  content: "";
  position: absolute;
  left: -6px;
  top: -3px;
  z-index: -1;
  width: calc(100% + 12px);
  height: calc(100% + 6px);
  background-color: #BC572B;
  transform: scaleX(0);
  transition-duration: 0.3s;
  transition-timing-function: ease;
}
.header__link:focus:after {
  transform: scaleX(1);
}
.header__link:focus {
  outline: none;
}
.header__link:active {
  color: #CE4307;
  outline: none;
  background-color: inherit;
}
.header__link:active::after {
  display: none;
}
.header__list-socials {
  display: flex;
}
.header__link-socials {
  display: inline-block;
  width: 50px;
  height: 50px;
  background-repeat: no-repeat;
  background-position: 95% 5%;
  transition-duration: 0.3s;
  transition-timing-function: ease;
  outline: none;
}
