.footer {
  padding-top: 23px;
  padding-bottom: 26px;
  background-color: #202020;
}
.footer__logo {
  display: flex;
  flex-direction: column;
  padding-top: 3px;
}
.footer__logo-text {
  font-family: "TT Firs Neue";
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  color: #E9E9E9;
}
.footer .container {
  display: flex;
  justify-content: space-between;
}
.footer__desc {
  flex-basis: 37.5%;
  padding-right: 108px;
}
.footer__desc-text {
  font-family: "Roboto";
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: #CACACA;
}
.footer__span {
  font-family: "Roboto";
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: #999;
}
.footer__social-text {
  font-family: "Roboto";
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: #CACACA;
}
.footer__list {
  display: flex;
  justify-content: space-around;
  padding-top: 8px;
  padding-right: 4px;
  padding-left: 3px;
}
.footer__link {
  display: inline-block;
  width: 30px;
  height: 30px;
  transition-duration: 0.3s;
  transition-timing-function: ease;
  background-repeat: no-repeat;
  outline: none;
